import { Router, Route, Switch } from "wouter"

import Navigation from "./components/Navigation";
import FAQ from "./pages/FAQ";
import Home from './pages/Home';

function App() {
  return (
    <Router>

      <Navigation />

      <Switch>
        <Route path="/" component={Home} />
        <Route path="/paso-a-paso" component={FAQ} />
      </Switch>

    </Router>
  );
}

export default App;
